import Vue from 'vue';
import {Lazyload} from 'vant';
import {mapState} from 'vuex'
import iMedia from './../../components/cell/i-media2.vue'

Vue.use(Lazyload);
export default {
  name: "BillboardList",
  props: ['type'],
  components: {
    iMedia
  },
  filters: {
    filterDate(date, cut = 0) {
      return new Date(date).getFullYear() + cut
    },
  },
  data() {
    return {
      isShow: true,
      iTotal: 0,
      iPageNo: 0,
      iPageNum: 10,
      oList: [],
      isLoading: false,
      finished: false,
    }
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo,
      nera: (state) => state.nera
    })
  },
  watch: {
    nera(v) {
      return v
    },
    async type(v) {
      this.iPageNo = 0
      this.finished = false
      this.isShow = false
      this.oList = []
      // console.log(11);
      this.$nextTick(() => {
        this.isShow = true
        this.load()
      })

    }
  },
  mounted() {
    // console.log(this.nera);
    // console.log(this.type);
    this.load()

  },
  methods: {
    async load() {
      // if (this.isLoading) return
      this.finished = false
      this.isLoading = true
      await this.getList();
    },
    getUrl() {
      switch (this.type) {
        case 'recommend':     //获取推荐的悬赏令
          return '/v1/emall/GetRecommendRewardsList'
        case 'unanswered':    //获取无回复的悬赏令
          return '/v1/emall/GetNoAnswerRewardsList'
        case 'unconfirmed':   //获取未确认的悬赏令
          return '/v1/emall/GetUnconfirmedRewardsList'
        case 'confirmed':     //获取已确认的悬赏令
          return '/v1/emall/GetConfirmedRewardsList'
        default:
          return '/v1/emall/GetNoAnswerRewardsList'
      }
    },
    async getList() {

      let res = await this.$axios.post(this.getUrl(), {
        iPageNo: this.iPageNo,
        iPageNum: this.iPageNum,
        iType: 2
      })
      let list = res.vRewardInfor.map(v => {
        return {
          ...v,
          iPastStatus: v.iExpirationDate < (new Date() / 1)
        }
      })
      this.oList = [...this.oList, ...list]
      this.iTotal = res.iTotal
      this.iPageNo++;
      this.isLoading = false

      if (this.oList.length >= this.iTotal) {
        this.finished = true
      }

      // console.log(res);
    },
    goDetail(v) {
      this.$router.push(`/reward/detail/${v.iRewardId}`)
    },
    goAnswer(v) {
      this.$router.push({
        path: '/answer',
        query: {
          iRewardId: v.iRewardId
        }
      })
    }
  },
}
