import {ImagePreview} from 'vant';

// import * as THREE from "three";
// import {GLTFLoader} from "three/examples/jsm/loaders/GLTFLoader";
// import {OrbitControls} from "three/examples/jsm/controls/OrbitControls";

export default {
  components: {
    [ImagePreview.Component.name]: ImagePreview.Component,
  },
  props: {
    url: {
      type: String,
      default: () => "",
    },
    isPopVideo: {
      type: Boolean,
      default: () => false,
    },
    isPopImg: {
      type: Boolean,
      default: () => false,
    },
  },
  data() {
    return {
      dialogVisible: false,
      isPlaying: false,
      //thress
      camera: null,
      scene: null,
      renderer: null,
      mush: null,
      loader: null,
      mixer: null,
      // clock: new THREE.Clock(),
      controls: null,
      time: new Date().getTime(),
      duration: 0,
      currentTime: 0
    };
  },
  computed: {
    controlsWidth() {
      return {
        width: (this.currentTime / this.duration * 100).toFixed(2) + '%'
      }
    }
  },
  mounted() {
    if (this.url) {
      // this.init();
      // this.render();
    }
  },
  watch: {
    url(ne, ol) {
      if (!ne) return;
      this.$nextTick(() => {
        // this.init();
        // this.render();
      })
    },
  },
  methods: {
    handleShowPic() {
      ImagePreview([this.url]);
    },
    getDuration() {
      if (this.$refs.player) {
        this.duration = this.$refs.player.duration;
      }
    },
    updateTime(e) {
      this.currentTime = e.target.currentTime;
    },
    handleOnePlay() {
      if (this.isPlaying) {
        this.$refs.player.pause()
        this.isPlaying = false
      } else {
        this.$refs.player.play()
        this.isPlaying = true
      }
    },
    getType() {
      if (/\.(gif|jpg|jpeg|png|GIF|JPEG|JPG|PNG|svg|SVG)$/.test(this.url)) {
        return "img";
      } else if (
        /\.(mp4|webm|wav|ogg|MP4|MOV|WEBM|WAV|OGG)$/.test(this.url)
      ) {
        return "video";
      } else if (/\.(mp3|MP3)$/.test(this.url)) {
        return 'mp3';
      } else {
        return "3D";
      }
    },
    createRect() {
      let geometry = new THREE.PlaneGeometry(1000, 1000);
      let material = new THREE.MeshBasicMaterial({color: 0xAAAAAA});
      let rect = new THREE.Mesh(geometry, material);
      return rect;
    },

    handleClose() {
      this.$refs.popvideo.pause();
      this.dialogVisible = false;
    },
    playVideo() {
      if (!this.isPopVideo) return false;
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.$refs.popvideo.currentTime = 0;
        this.$refs.popvideo.play();
      });
    },
    // 模型初始化
    init() {
      if (this.getType() !== "3D") {
        return;
      }
      let container = document.getElementById("container" + this.time);

      // 设置相机
      this.camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);

      this.camera.position.z = 5;
      this.scene = new THREE.Scene();
      let self = this;


      // 加载模型
      var loader = new GLTFLoader();//.setPath("/static/dalou/");
      //   loader.setCrossOrigin( 'anonymous' )
      // loader.setCrossOrigin( 'use-credentials' )
      loader.load(this.url, gltf => {
        //   loader.load('./static/Bee.glb', gltf=> {
        // console.log('-------------' + gltf);
        this.scene.add(gltf.scene);

      });

      var light = new THREE.AmbientLight(0x404040, 10); // soft white light
      this.scene.add(light);

      var directionalLight = new THREE.DirectionalLight(0xffffff);
      this.scene.add(directionalLight);


      var spotLight = new THREE.SpotLight(0xffffff);
      spotLight.position.set(100, 1000, 100);

      spotLight.castShadow = true;

      spotLight.shadow.mapSize.width = 1024;
      spotLight.shadow.mapSize.height = 1024;

      spotLight.shadow.camera.near = 500;
      spotLight.shadow.camera.far = 4000;
      spotLight.shadow.camera.fov = 30;

      this.scene.add(spotLight);


      var light = new THREE.PointLight(0xff0000, 1, 100);
      light.position.set(50, 50, 50);
      this.scene.add(light);


      /**
       * 相机设置
       */
      var width = window.innerWidth; //窗口宽度
      var height = window.innerHeight; //窗口高度
      var k = width / height; //窗口宽高比
      var s = 150; //三维场景显示范围控制系数，系数越大，显示的范围越大

      this.renderer = new THREE.WebGLRenderer({antialias: true});
      this.renderer.setSize(container.clientWidth, container.clientHeight);
      container.appendChild(this.renderer.domElement);
      this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    },
    render() {
      if (this.getType() !== "3D") {
        return;
      }
      requestAnimationFrame(this.render);
      this.renderer.render(this.scene, this.camera); //执行渲染操作

    },
  },
};
